.courses {
  padding-top: 0px;
  padding-bottom: 84px;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  &--header {
    width: 60%;
    padding-bottom: 12px;
    height: 5%;
    display: flex;
    align-items: center;
    &--directory {
      font-size: 16px;
      font-family: "Open Sans", sans-serif;
      color: rgb(0, 0, 0);
      cursor: pointer;
    }
    &--city {
      font-size: 16px;
      padding-left: 12px;
      font-family: "Open Sans", sans-serif;
      color: rgb(120, 188, 65);
      cursor: pointer;
    }
  }
  &--container {
    display: flex;
  }
  &--wrapper {
    width: 60%;
    display: flex;
    &--lists {
      width: 35%;
      &--club {
        display: flex;
        width: 90%;
        align-items: center;
        font-size: 24px;
        font-family: "Bebas Neue";
        color: rgb(0, 0, 0);
        line-height: 1.025;
        padding: 8px 0px 8px 0px;
        cursor: pointer;
      }
      &--club:hover {
        color: rgb(120, 188, 65);
      }
      &--sub {
        font-size: 16px;
        font-family: "Open Sans", sans-serif;
        color: rgb(0, 0, 0);
        cursor: pointer;
      }
      &--sub-shrink {
        height: 0;
        font-size: 16px;
        font-family: "Open Sans", sans-serif;
        color: rgb(0, 0, 0);
        cursor: pointer;
        overflow: hidden;
      }
      &--sub-title {
        font-size: 16px;
        font-family: "Open Sans", sans-serif;
        color: rgb(0, 0, 0);
        cursor: pointer;
      }
    }
    &--details {
      width: 65%;
      display: flex;
      flex-direction: column;
      &--left {
        width: 50%;
        display: flex;
        flex-direction: column;
        border-right: 2px solid rgba(0, 0, 0, 0.3);
        padding-left: 24px;
        &--title {
          font-size: 28px;
          font-family: "Bebas Neue";
          color: rgb(0, 0, 0);
          padding: 0px 0px 8px 0px;
          margin-bottom: 10%;
          width: 90%;
        }
        &--googleURL {
          img {
            height: 1.3rem;
            padding-left: 4px;
            margin-bottom: -4px;
          }
        }
        &--desc {
          width: 90%;
          font-size: 14px;
          font-family: "Open Sans", sans-serif;
          color: rgb(0, 0, 0);
          line-height: 1.429;
          text-align: left;
          padding-bottom: 12px;
        }
        &--sub {
          font-size: 14px;
          font-family: "Open Sans", sans-serif;
          color: rgb(0, 0, 0);
          line-height: 1.429;
          text-align: left;
          // display: flex;
          // align-items: center;
        }
        &--main {
          display: flex;
          align-items: center;
          font-size: 14px;
          font-weight: 600;
          font-family: "Open Sans", sans-serif;
          color: rgb(0, 0, 0);
          line-height: 1.429;
          text-align: left;
          padding-right: 8px;
        }
        &--index {
          display: flex;
          align-items: center;
          font-size: 16px;
          font-family: "Open Sans", sans-serif;
          color: rgb(0, 0, 0);
          line-height: 1.429;
          width: 60%;
          text-align: left;
          padding-bottom: 4px;
        }
        &--var {
          display: flex;
          align-items: center;
          width: 40%;
          font-size: 24px;
          font-family: "Bebas Neue";
          color: rgb(0, 0, 0);
          line-height: 1.429;
          text-align: left;
          padding-bottom: 4px;
        }
        &--row {
          width: 90%;
          display: flex;
          flex-direction: row;
        }
        &--map {
          width: 90%;
          display: flex;
          flex-direction: row;
        }
      }
      &--right {
        width: 50%;
        display: flex;
        padding-left: 48px;
        flex-direction: column;
        &--title {
          font-size: 28px;
          font-family: "Bebas Neue";
          color: rgb(0, 0, 0);
          padding: 0px 0px 8px 0px;
          margin-bottom: 10%;
        }
        &--designer {
          font-size: 24px;
          font-family: "Bebas Neue";
          color: rgb(0, 0, 0);
          line-height: 1.429;
          text-align: left;
          padding-bottom: 4px;
        }
        &--designer::before {
          display: block;
          content: "Designer :";
          opacity: 0.7;
          font-size: 16px;
          font-family: "Open Sans", sans-serif;
          color: rgb(0, 0, 0);
          line-height: 1.429;
          text-align: left;
        }
        &--onsite {
          font-size: 24px;
          font-family: "Bebas Neue";
          color: rgb(0, 0, 0);
          line-height: 1.429;
          text-align: left;
          padding-bottom: 4px;
        }
        &--onsite::before {
          display: block;
          content: "On Site Golf Professional :";
          opacity: 0.7;
          font-size: 16px;
          font-family: "Open Sans", sans-serif;
          color: rgb(0, 0, 0);
          line-height: 1.429;
          text-align: left;
        }
        &--manager {
          font-size: 24px;
          font-family: "Bebas Neue";
          color: rgb(0, 0, 0);
          line-height: 1.429;
          text-align: left;
          padding-bottom: 4px;
        }
        &--manager::before {
          display: block;
          content: "General Manager :";
          opacity: 0.7;
          font-size: 16px;
          font-family: "Open Sans", sans-serif;
          color: rgb(0, 0, 0);
          line-height: 1.429;
          text-align: left;
        }
        &--super {
          font-size: 24px;
          font-family: "Bebas Neue";
          color: rgb(0, 0, 0);
          line-height: 1.429;
          text-align: left;
          padding-bottom: 4px;
        }
        &--super::before {
          display: block;
          content: "Super Intendent :";
          opacity: 0.7;
          font-size: 16px;
          font-family: "Open Sans", sans-serif;
          color: rgb(0, 0, 0);
          line-height: 1.429;
          text-align: left;
        }
      }
    }
  }
  .arrow {
    border: solid black;
    border-width: 0 3px 3px 0;
    display: inline-block;
    padding: 4px;
  }
  .right {
    transform: rotate(-45deg);
    transition: 0.2s ease-in-out;
  }

  .left {
    transform: rotate(135deg);
  }

  .up {
    transform: rotate(-135deg);
  }

  .down {
    transition: 0.2s ease-in-out;
    transform: rotate(45deg);
  }
  .list-wrapper {
    // width: 50%;
  }
}

@include respond-between(xlg, xxlg) {
  .courses {
    &--header {
      width: 70%;
    }
    &--wrapper {
      width: 70%;
    }
  }
}

@include respond-between(lg, xlg) {
  .courses {
    &--header {
      width: 75%;
    }
    &--wrapper {
      width: 75%;
      &--details {
        &--left {
          padding-left: 0%;
        }
      }
    }
  }
}

@include respond-between(md, lg) {
  .courses {
    padding-top: 32px;
    padding-bottom: 48px;
    &--container {
      flex-direction: row;
    }
    &--header {
      width: 85%;
    }
    &--wrapper {
      width: 100%;
      flex-direction: column;
      align-items: center;
      &--lists {
        width: 85%;
        display: flex;
        flex-wrap: wrap;
        border-bottom: 2px solid rgba(0, 0, 0, 0.3);
        &--club {
          width: 100%;
        }
      }
      &--details {
        width: 100%;
        flex-direction: column;
        &--left {
          width: 85%;
          padding-left: 9%;
          border-right: unset;
          &--title {
            margin-bottom: 2%;
            padding-top: 2%;
            margin-left: -2%;
          }
        }
        &--right {
          width: 85%;
          display: flex;
          flex-wrap: wrap;
          border-right: unset;
          padding-left: 0%;
          &--title {
            margin-bottom: 2%;
            padding-top: 2%;
            margin-left: -2%;
          }
        }
      }
    }
  }
  .list-wrapper {
    width: 50%;
  }
}

